.form-toggle__wrapper {
  position: relative;
}
.form-toggle__wrapper label {
  cursor: default;
}

.form-toggle {
  cursor: pointer;
  display: block;
  position: absolute;
  top: 0;
  bottom: -1px;
  left: 0;
  right: 0;
  text-align: left;
  text-indent: -100000px;
  z-index: 2;
}
.form-toggle:focus {
  box-shadow: none;
}
.form-toggle.disabled {
  cursor: default;
}

.form-toggle__switch {
  align-self: flex-start;
  background: #999;
  border-radius: 12px;
  box-sizing: border-box;
  display: inline-block;
  padding: 2px;
  outline: 0;
  position: relative;
  width: 40px;
  height: 24px;
  transition: all 0.4s ease, box-shadow 0s;
  vertical-align: middle;
}
.form-toggle__switch::before, .form-toggle__switch::after {
  content: "";
  display: block;
  position: relative;
  width: 20px;
  height: 20px;
}
.form-toggle__switch::after {
  border-radius: 50%;
  background: #fff;
  left: 0;
  transition: all 0.2s ease;
}
.form-toggle__switch::before {
  display: none;
}
.accessible-focus .form-toggle__switch:focus {
  box-shadow: 0 0 0 2px var(--wp-admin-theme-color);
}

.form-toggle__label {
  vertical-align: bottom;
  z-index: 1;
}
.form-toggle__label .form-toggle__label-content {
  color: #2e4453;
  flex: 0 1 100%;
  font-size: 13px;
  line-height: 16px;
  margin-left: 12px;
  margin-right: 8px;
  vertical-align: top;
  text-transform: uppercase;
}
@media only screen and (max-width: 480px) {
  .form-toggle__label .form-toggle__label-content {
    display: none;
  }
}

.accessible-focus .form-toggle:focus + .form-toggle__label .form-toggle__switch {
  box-shadow: 0 0 0 2px var(--wp-admin-theme-color);
